"use client";

import { useState, useMemo, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { PlusCircle, UserPlus } from "lucide-react";
import { useAuth } from "@/contexts/auth-context";
import { sessions as initialSessions, users as initialUsers } from "@/lib/data";
import type { Session, User } from "@/lib/types";
import SessionList from "@/components/dashboard/session-list";
import { SessionFilters } from "@/components/dashboard/session-filters";
import CreateSessionDialog from "@/components/dashboard/create-session-dialog";
import CreateUserDialog from "@/components/dashboard/create-user-dialog";

const USERS_STORAGE_KEY = 'work-session-users-list';

export default function DashboardPage() {
  const { user } = useAuth();
  const [sessions, setSessions] = useState<Session[]>(initialSessions);
  const [users, setUsers] = useState<User[]>(() => {
    if (typeof window === 'undefined') {
      return initialUsers;
    }
    try {
      const storedUsers = localStorage.getItem(USERS_STORAGE_KEY);
      return storedUsers ? JSON.parse(storedUsers) : initialUsers;
    } catch (error) {
      console.error("Failed to parse users from localStorage", error);
      return initialUsers;
    }
  });

  const [isMounted, setIsMounted] = useState(false);
  const [filters, setFilters] = useState<{
    status: string;
    hostId: string;
    clientId: string;
    date: Date | undefined;
  }>({
    status: "all",
    hostId: "all",
    clientId: "all",
    date: undefined,
  });

  useEffect(() => {
    setIsMounted(true);
    // Listen for storage changes to update users list from other tabs
    const handleStorageChange = (e: StorageEvent) => {
        if (e.key === USERS_STORAGE_KEY && e.newValue) {
            try {
                setUsers(JSON.parse(e.newValue));
            } catch (error) {
                console.error("Failed to parse users from storage event", error);
            }
        }
    };
    window.addEventListener('storage', handleStorageChange);
    return () => window.removeEventListener('storage', handleStorageChange);
  }, [])

  useEffect(() => {
    try {
      localStorage.setItem(USERS_STORAGE_KEY, JSON.stringify(users));
    } catch (error) {
      console.error("Failed to save users to localStorage", error);
    }
  }, [users]);


  const filteredSessions = useMemo(() => {
    return sessions.filter((session) => {
      const statusMatch =
        filters.status === "all" || session.status === filters.status;
      const hostMatch =
        filters.hostId === "all" || session.hostId === filters.hostId;
      const clientMatch =
        filters.clientId === "all" || session.clientId === filters.clientId;
      const dateMatch =
        !filters.date ||
        new Date(session.date).toDateString() === filters.date.toDateString();
      return statusMatch && hostMatch && clientMatch && dateMatch;
    });
  }, [sessions, filters]);

  const handleSessionCreated = (newSession: Session) => {
    setSessions((prev) => [newSession, ...prev]);
  };
  
  const handleSessionUpdated = (updatedSession: Session) => {
    setSessions((prev) => prev.map(s => s.id === updatedSession.id ? updatedSession : s));
  };
   
  const handleUserCreated = (newUser: User) => {
    setUsers((prev) => [newUser, ...prev]);
  };
  
  if (!user || !isMounted) return null;

  return (
    <div className="flex flex-col gap-6">
      <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
        <div>
          <h1 className="text-3xl font-bold font-headline">Session Dashboard</h1>
          <p className="text-muted-foreground">
            View, manage, and book your work sessions.
          </p>
        </div>
        {(user.role === "admin" || user.role === 'varsity') && (
          <div className="flex gap-2">
            <CreateSessionDialog onSessionCreated={handleSessionCreated} users={users}>
              <Button>
                <PlusCircle className="mr-2" />
                Create Session
              </Button>
            </CreateSessionDialog>
          </div>
        )}
      </div>

      {(user.role === "admin" || user.role === 'varsity') && (
        <SessionFilters
          onFilterChange={setFilters}
          users={users}
        />
      )}

      <SessionList sessions={filteredSessions} onSessionUpdated={handleSessionUpdated} users={users} />
    </div>
  );
}
