"use client";

import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { cn } from "@/lib/utils";
import { Calendar as CalendarIcon } from "lucide-react";
import { format } from "date-fns";
import React from "react";
import type { Session, User } from "@/lib/types";
import { useToast } from "@/hooks/use-toast";
import { useAuth } from "@/contexts/auth-context";


const sessionSchema = z.object({
  title: z.string().min(3, "Title must be at least 3 characters."),
  description: z.string().min(10, "Description must be at least 10 characters."),
  date: z.date({ required_error: "A date is required." }),
  time: z.string().regex(/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]\s(AM|PM)$/, "Invalid time format (e.g., 02:00 PM)."),
  hostId: z.string().nullable(),
  clientId: z.string().nullable(),
});

interface CreateSessionDialogProps {
    children: React.ReactNode;
    sessionToEdit?: Session;
    onSessionCreated?: (session: Session) => void;
    onSessionUpdated?: (session: Session) => void;
    users: User[];
}

export default function CreateSessionDialog({ children, sessionToEdit, onSessionCreated, onSessionUpdated, users }: CreateSessionDialogProps) {
  const [open, setOpen] = React.useState(false);
  const { toast } = useToast();
  const { user } = useAuth();

  const form = useForm<z.infer<typeof sessionSchema>>({
    resolver: zodResolver(sessionSchema),
    defaultValues: {
      title: "",
      description: "",
      date: new Date(),
      time: "10:00 AM",
      hostId: null,
      clientId: null,
    },
  });
  
  const isEditing = !!sessionToEdit;

  React.useEffect(() => {
    if (open) {
        if (isEditing) {
            form.reset({
                title: sessionToEdit.title,
                description: sessionToEdit.description,
                date: new Date(sessionToEdit.date),
                time: sessionToEdit.time,
                hostId: sessionToEdit.hostId,
                clientId: sessionToEdit.clientId,
            });
        } else {
            form.reset({
                title: "",
                description: "",
                date: new Date(),
                time: "10:00 AM",
                hostId: user?.role === 'varsity' ? user.id : null,
                clientId: null,
            });
        }
    }
  }, [sessionToEdit, form, open, isEditing, user]);

  function onSubmit(values: z.infer<typeof sessionSchema>) {
    const isBooked = !!values.hostId && !!values.clientId;
    const sessionData = {
        ...values,
        date: format(values.date, 'yyyy-MM-dd'),
        status: isBooked ? 'booked' : 'available',
        messages: sessionToEdit?.messages || [],
    } as Omit<Session, 'id'>;

    if (sessionToEdit) {
      const updatedSession = { ...sessionToEdit, ...sessionData };
      onSessionUpdated?.(updatedSession);
      toast({ title: "Session Updated", description: "The session has been successfully updated." });
    } else {
      const newSession = { ...sessionData, id: Date.now().toString() };
      if (user?.role === 'varsity') {
        newSession.hostId = user.id;
      }
      onSessionCreated?.(newSession);
      toast({ title: "Session Created", description: "The new session has been added to the dashboard." });
    }
    setOpen(false);
  }
  
  const canSelectHost = user?.role === 'admin' && isEditing;
  const canSelectClient = user?.role === 'admin' && isEditing;

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>{children}</DialogTrigger>
      <DialogContent className="sm:max-w-2xl">
        <DialogHeader>
          <DialogTitle className="font-headline">{sessionToEdit ? 'Edit Session' : 'Create New Session'}</DialogTitle>
          <DialogDescription>
            {sessionToEdit ? 'Update the details for this session.' : 'Fill in the details to create a new work session.'}
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="grid grid-cols-1 md:grid-cols-2 gap-4 py-4">
            <FormField control={form.control} name="title" render={({ field }) => (
                <FormItem className="md:col-span-2">
                  <FormLabel>Title</FormLabel>
                  <FormControl><Input {...field} /></FormControl>
                  <FormMessage />
                </FormItem>
            )} />
            <FormField control={form.control} name="description" render={({ field }) => (
                <FormItem className="md:col-span-2">
                  <FormLabel>Description</FormLabel>
                  <FormControl><Textarea {...field} /></FormControl>
                  <FormMessage />
                </FormItem>
            )} />

             <FormField control={form.control} name="date" render={({ field }) => (
                <FormItem className="flex flex-col">
                    <FormLabel>Date</FormLabel>
                    <Popover>
                        <PopoverTrigger asChild>
                            <FormControl>
                                <Button variant={"outline"} className={cn("pl-3 text-left font-normal", !field.value && "text-muted-foreground")}>
                                    {field.value ? format(field.value, "PPP") : <span>Pick a date</span>}
                                    <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                                </Button>
                            </FormControl>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="start">
                            <Calendar mode="single" selected={field.value} onSelect={field.onChange} initialFocus />
                        </PopoverContent>
                    </Popover>
                    <FormMessage />
                </FormItem>
            )} />
            <FormField control={form.control} name="time" render={({ field }) => (
                <FormItem>
                  <FormLabel>Time</FormLabel>
                  <FormControl><Input placeholder="e.g., 02:00 PM" {...field} /></FormControl>
                  <FormMessage />
                </FormItem>
            )} />
            {canSelectHost && (
              <FormField control={form.control} name="hostId" render={({ field }) => (
                  <FormItem>
                    <FormLabel>Host (Varsity)</FormLabel>
                    <Select onValueChange={(value) => field.onChange(value === "none" ? null : value)} value={field.value || "none"}>
                        <FormControl><SelectTrigger><SelectValue placeholder="Assign a host" /></SelectTrigger></FormControl>
                        <SelectContent>
                            <SelectItem value="none">None</SelectItem>
                            {users.filter(u => u.role === 'varsity').map(user => (
                                <SelectItem key={user.id} value={user.id}>{user.name}</SelectItem>
                            ))}
                        </SelectContent>
                    </Select>
                    <FormMessage />
                  </FormItem>
              )} />
            )}
             {canSelectClient && (
              <FormField control={form.control} name="clientId" render={({ field }) => (
                  <FormItem>
                    <FormLabel>Client (Novice)</FormLabel>
                    <Select onValueChange={(value) => field.onChange(value === "none" ? null : value)} value={field.value || "none"}>
                        <FormControl><SelectTrigger><SelectValue placeholder="Assign a client" /></SelectTrigger></FormControl>
                        <SelectContent>
                            <SelectItem value="none">None</SelectItem>
                            {users.filter(u => u.role === 'novice').map(user => (
                                <SelectItem key={user.id} value={user.id}>{user.name}</SelectItem>
                            ))}
                        </SelectContent>
                    </Select>
                    <FormMessage />
                  </FormItem>
              )} />
            )}
             <DialogFooter className="md:col-span-2 pt-4">
                <DialogClose asChild><Button type="button" variant="outline">Cancel</Button></DialogClose>
                <Button type="submit">{isEditing ? 'Save Changes' : 'Create Session'}</Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
